
.title {
    font-size: calc(var(--size)*2);
    letter-spacing: 2px;
    margin-bottom: var(--size);
  }
  
  .orange {
    color: var(--orange);
    animation: flicker 1.5s infinite alternate; 
  }
  
  .blue {
    color: var(--blue);
    text-shadow: 
      0 0 calc(var(--size)) var(--blue),
      0 0 calc(var(--size)*3) var(--blue),
      0 0 calc(var(--size)*6) var(--blue);
  }
  
  @keyframes flicker {
    0%, 18%, 22%, 25%, 53%, 57%, 100% {
      text-shadow:
      0 0 calc(var(--size)) var(--orange),
      0 0 calc(var(--size)*3) var(--orange),
      0 0 calc(var(--size)*6) var(--orange);
    }
    20%, 24%, 55% {       
      text-shadow: none;
    }
  }